import * as types from './mutation-types.js'
import axios from 'axios'

export const getLocation = function ({commit, state}) {
	axios.get('/http://apis.map.qq.com/ws/location/v1/ip?key=VADBZ-UEALJ-7DVFA-KSCCY-5XTE2-2OFOV&ip=' + returnCitySN.cip)
		.then( (res) => {
			let location =  res.data.result.location.lat + ',' + res.data.result.location.lng;
			commit(types.SET_LOCATION, location);
			axios.get('/http://apis.map.qq.com/ws/geocoder/v1/?key=VADBZ-UEALJ-7DVFA-KSCCY-5XTE2-2OFOV&location=' + location)
			.then( (res) => { 
				commit(types.SET_ADDRESS, res.data.result.address);
			})
		})
}

export const loginUser = function ({commit, state}, user) {
	commit(types.SET_USER, user);
	commit(types.SET_LOGIN, true);
}

export const logoutUser = function ({commit, state}) {
	commit(types.SET_USER, {});
	commit(types.SET_LOGIN, false);
}


